@import '../../styles/variables.scss';

.scoreTable {
  position: absolute;
  width: 100%;

  .scoreTableList {
    background-color: $primaryWhite;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
  }

  .navigation {
    display: flex;
    justify-content: flex-end;
    padding: 1rem;

    .navigationMenuButton {
      width: 14px;
      height: 14px;
      background-image: url(../../assets/MenuIcon.svg);
    }
  }
}

.isOpen {
  height: 100vh;
  background-color: $primaryWhite;
  .navigation .navigationMenuButton {
    background-image: url(../../assets/CloseIcon.svg);
  }
}
